import React, { ReactElement, useState, useEffect } from "react";
import {
  Dropdown
} from "react-bootstrap";
import { Auth } from "aws-amplify";
import Home from "./Home/Home";
import SignUp from "./SignUp/SignUp";
import SignIn from "./SignIn/SignIn";
import ConfirmSignUp from "./ConfirmSignUp/ConfirmSignUp";
import { UserDetails, UserDetailsDefault } from "./types";
import "./App.scss";

const App = () : ReactElement => {
  const [page, setPage] = useState("SignIn");
  const [userDetails, setUserDetails] = useState<UserDetails>({
    email: "",
    signIn: false
  });

  useEffect(() => {
    Auth.currentSession()
      .then((response) => {
        console.log(response);
        setUserDetails({
          email: response.getAccessToken().payload.username,
          signIn: true
        });
        setPage("Home");
      })
      .catch((error) => {
        console.log(error);
      });
  }, []);

  const signOut = () => {
    Auth.signOut()
      .then(() => {
        setUserDetails(UserDetailsDefault);
        setPage("SignIn");
      });
  };

  return (
    <div className="app">
      <div className="app-header">
        {
          userDetails.signIn
          && (
          <>
            <span className="info-link">
              Account:
              {" "}
            </span>
            <Dropdown>
              <Dropdown.Toggle className="link">
                {userDetails.email}
              </Dropdown.Toggle>
              <Dropdown.Menu>
                <Dropdown.Item onClick={signOut}>Sign Out</Dropdown.Item>
              </Dropdown.Menu>
            </Dropdown>
          </>
          )
        }
      </div>
      <div className="app-body">
        {page === "Home" && <Home />}
        {page === "SignIn" && <SignIn setPage={setPage} userDetails={userDetails} setUserDetails={setUserDetails} />}
        {page === "SignUp" && <SignUp setPage={setPage} userDetails={userDetails} setUserDetails={setUserDetails} />}
        {page === "ConfirmSignUp" && <ConfirmSignUp setPage={setPage} email={userDetails.email} />}
      </div>
    </div>
  );
};

export default App;
