import React, { ReactElement, FunctionComponent, useState } from "react";
import {
  Button,
  Form
} from "react-bootstrap";
import { Auth } from "aws-amplify";
import { UserDetails } from "../types";
import "./SignIn.scss";

const SignIn: FunctionComponent<{
  setPage: (val: string) => void,
  userDetails: UserDetails,
  setUserDetails: (val: UserDetails) => void
}> = ({
  setPage, userDetails, setUserDetails
}): ReactElement => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [signInError, setSignInError] = useState("");

  const submitDisabled = !email || !password;

  const signIn = () => {
    const success = () => {
      setUserDetails({
        ...userDetails,
        email,
        signIn: true
      });
      setPage("Home");
    };
    if (process.env.NODE_ENV === "development" && email === "test@test.com") {
      Promise.resolve("").then(() => {
        success();
      });
    } else {
      Auth.signIn({
        username: email,
        password
      })
        .then((response) => {
          console.log(response);
          success();
        })
        .catch((error) => {
          console.log("error signing up:", error);
          if (error.code === "UserNotConfirmedException") {
            setUserDetails({
              ...userDetails,
              email
            });
            setPage("ConfirmSignUp");
          } else {
            setSignInError(error.message);
          }
        });
    }
  };

  return (
    <Form>
      <h3>Sign In</h3>

      <Form.Group>
        <span className="info-link">
          Do not have an account?
          {" "}
        </span>
        <Button onClick={() => setPage("SignUp")} className="link">Register</Button>
      </Form.Group>

      <Form.Group className="form-group">
        <Form.Label>Email address</Form.Label>
        <Form.Control
          type="email"
          className="form-control"
          placeholder="Enter email"
          onChange={(e) => setEmail(e.target.value)}
        />
      </Form.Group>

      <Form.Group className="form-group">
        <Form.Label>Password</Form.Label>
        <Form.Control
          type="password"
          className="form-control"
          placeholder="Enter password"
          onChange={(e) => setPassword(e.target.value)}
        />
      </Form.Group>

      <Button disabled={submitDisabled} onClick={signIn}>Submit</Button>

      <Form.Group>
        <Form.Label className="error">{signInError}</Form.Label>
      </Form.Group>
    </Form>
  );
};

export default SignIn;
